import React from "react"
import { navigate } from 'gatsby'


export const withUnAuth = Component => props => {
  if(!props.getCurrentUser){
    navigate('/')
    return <></>
}
  return (
  <Component {...props}  />
  )
}

export const withAuth = Component => props => {
  if(props.getCurrentUser){
      navigate('/')
    return <></>
}
    return (
    <Component  {...props}  />
    )
  }