import React , { useRef } from "react"
import { Link } from 'gatsby'
import stl from '../../STYLES/sidebar.module.scss'
import Burger from '../../assets/burger.svg'
import Close from '../../assets/close.svg'
import {deleteCookie } from '../../helpers/index'




const SideBar = ({getCurrentUser , resetStore}) => {

 const burger = useRef(null)
 const sideBar = useRef(null)

const openSideBar = () => {
  burger.current.classList.toggle(stl.hide)
  sideBar.current.classList.toggle(stl.toggler)
}

const closeSideBar = () => {
    burger.current.classList.toggle(stl.hide)
    sideBar.current.classList.toggle(stl.toggler)
}

const logout = () => {
  deleteCookie("token")
  resetStore()
}



return (
  <aside ref = {sideBar} className = {stl.sidebar}>
    <span ref = {burger}>
        <Burger onClick = {() => openSideBar()} className = {stl.burger} />
    </span>
    <span>
        <Close onClick = {() => closeSideBar()} className = {stl.close}  />
    </span>
   <ul>
      <li>
        <Link to = '/'>MAIN</Link>
      </li>
      <li>
        <Link to = '/jobs'>Jobs</Link>
      </li>
      <li style = {{marginBottom:"10px"}}>
        <Link to = '/employees'>Employees</Link>
      </li>
      <div style = {{ marginBottom:"10px" ,height:"1px" , background:"black" , width:"100%"}}></div>
      {!getCurrentUser
       ? <li>
            <Link to = '/auth/sign-up'>sign-in/up</Link>
         </li>
       : <>
         <li>
              <Link to = '/create-job'>Create job ad </Link>
           </li>

         <li>
            <Link to = '/profile'>My Profile</Link>
         </li>
         <li onClick ={() => logout()}>
            Log out
         </li>
         </>
         }
  </ul>
  </aside>
)}



export default SideBar